.media {
  padding-bottom: 48px;
  &-wrapper {
    &--header {
      margin-bottom: 32px;
      h2 {
        font-family: "Hind Siliguri", sans-serif;
        font-weight: 600;
        font-size: 22px;
        line-height: 1.45;
        width: 100%;
        text-align: center;
        color: #43425f;
        margin-bottom: 5px;
      }
      p {
        font-family: "Sarabun", sans-serif;
        font-weight: 500;
        width: 100%;
        text-align: center;
        font-size: 12px;
        line-height: 1.83;
        color: rgba(#43425f, 0.8);
      }
    }

    &--body {
      padding-left: 24px;
      .media-body--block {
        padding: 10px;
        width: calc(100% - 24px);
        margin-right: auto;
        background: $brand-white;
        box-shadow: 0px 1px 2px rgba(93, 88, 88, 0.2);
        border-radius: 3px;
        margin-bottom: 12px;
        box-sizing: border-box;

        .media-block--header {
          position: relative;
          margin-bottom: 7px;
          width: 100%;
          height: 134px;
          border-radius: 3px;
          overflow: hidden;

          img {
            height: 100%;
            object-fit: cover;
          }

          span {
            position: absolute;
            left: 10px;
            top: 10px;
            width: 100%;
            max-width: 64px;
            display: block;
            padding-top: 4px;
            padding-bottom: 4px;
            border-radius: 2px;
            font-family: "Sarabun", sans-serif;
            font-weight: 500;
            font-size: 10px;
            line-height: 1.3;
            text-align: center;
            color: $brand-white;
          }
        }

        .media-block--body {
          h3 {
            font-family: "Sarabun", sans-serif;
            font-weight: 600;
            font-size: 14px;
            line-height: 18px;
            color: #091540;
            margin-bottom: 4px;
          }

          p {
            font-family: "Sarabun", sans-serif;
            font-size: 10px;
            line-height: 1.5;
            color: #616884;
            margin-bottom: 12px;
          }

          time {
            font-family: "Sarabun", sans-serif;
            font-weight: 600;
            font-size: 8px;
            line-height: 1;
            color: #324ea3;
          }
        }
      }

      .media-body--slider {
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        .media-body--block {
          width: 150px;
          padding: 8px 5px;
          margin-right: 10px;

          &:last-of-type{
            margin-right: 0;
          }
        }

        .media-block--header {
          width: 140px;
          height: 104px;
          border-radius: 2px;

          span {
            top: 5px;
            left: 5px;
            font-size: 9px;
            max-width: 57px;
            line-height: 1;
          }
        }

        h4 {
          font-family: "Sarabun", sans-serif;
          font-weight: 600;
          font-size: 10px;
          line-height: 13px;
          color: #091540;
          margin-bottom: 3px;
        }
      }

      .media-body--scroll {
        -webkit-appearance: none;
        width: 100%;
        height: 1px;
        background: rgba(#324ea3, 0.2);
        outline: none;
        opacity: 0.7;
        -webkit-transition: 0.2s;
        transition: opacity 0.2s;
        margin-bottom: 35px;

        &:hover {
          opacity: 1;
        }

        &::-webkit-slider-thumb {
          -webkit-appearance: none;
          appearance: none;
          width: 72px;
          height: 3px;
          background: #324ea3;
          cursor: pointer;
          border-radius: 2px;
        }

        &::-moz-range-thumb {
          width: 72px;
          height: 3px;
          background: #324ea3;
          cursor: pointer;
        }
      }

    }

    &--footer {
      a {
        width: 100%;
        max-width: 100px;
        margin-left: auto;
        margin-right: auto;
        display: block;
        font-family: "Sarabun", sans-serif;
        font-weight: 600;
        font-size: 8.4px;
        line-height: 11px;
        color: $brand-white;
        padding-top: 7px;
        padding-bottom: 6px;
        background: #324ea3;
        box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.2);
        border-radius: 1.2px;
        transition: 0.4s ease;
        text-align: center;

        &:hover {
          box-shadow: none;
          background-color: darken(#324ea3, 10%);
        }
        &:active {
          transition: 0s;
          background-color: darken(#324ea3, 20%);
        }
      }
    }
  }
}
